  box-shadow: none !important;
}

.date-header {
  padding: 0 10px;
}

ion-list {
  padding-top: 0;
  background-color: #fff !important;
  --background: #fff !important;
  border-radius: 0;
  --border-radius: 0;
}
ion-list ion-card-subtitle {
  font-size: 18px;
}
ion-list.notice-card ion-item {
  border-bottom: 0;
  --background: transparent;
  border-radius: 4px;
  margin-bottom: 5px;
  align-items: flex-start;
}
ion-list.notice-card ion-item ion-avatar span {
  color: green;
}
ion-list.notice-card ion-item.admin {
  background-color: #badee2;
}
ion-list.notice-card ion-item.teacher {
  background-color: #eac0d4;
}
ion-list.notice-card ion-item.teacher ion-avatar span {
  color: red;
}
ion-list.notice-card ion-item.principal {
  background-color: #eae9c0;
}
ion-list.notice-card ion-item.principal ion-avatar span {
  color: #da9513;
}
ion-list.notice-card ion-item:last-child {
  border-bottom: 0 !important;
}
ion-list.notice-card ion-item ion-avatar {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #fff;
  width: 45px;
  height: 45px;
  margin-right: 5px;
}
ion-list.notice-card ion-item ion-avatar span {
  font-size: 20px;
  font-weight: bold;
}
ion-list.notice-card ion-item ion-label {
  font-size: 13px;
  font-weight: 400;
}
ion-list.notice-card ion-item.important {
  background: blue;
  --background: red;
}
@media (prefers-color-scheme: dark) {
  ion-list.notice-card ion-item.important {
    --background: green;
  }
}

.download {
  margin-top: 20px;
}
.download a {
  text-decoration: none;
  border: 1px solid #000;
  border-radius: 4px;
  background-color: var(--ion-color-dark);
  color: #fff;
  padding: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 16px;
}
.download a ion-icon {
  font-size: 24px;
}

.modal-text-header ion-card-subtitle {
  font-size: 16px;
}
.modal-text-header ion-card-subtitle ion-text {
  font-size: 14px;
  display: block;
  color: #000;
}

.uploaded-file-text {
  margin: 10px 0;
  font-size: 12px;
  border: 0;
  display: flex;
}
.uploaded-file-text .file-thumb {
  width: 64px;
  height: 64px;
  border: 1px solid #ccc;
  padding: 5px;
  border-radius: 4px;
  display: flex;
  position: relative;
  margin-right: 10px;
}
.uploaded-file-text .file-thumb img {
  width: 100%;
}

.bold-black-text {
  font-weight: bold;
  color: black;
}

ion-label {
  color: black;
